.container {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__hand--minute {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate-minute;
  position: absolute;
  top: calc(50% - 20vmin);
}

.stopwatch__hand--second {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate-second;
  position: absolute;
  top: calc(50% - 38vmin);
}

.stopwatch__center {
  z-index: 1;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

.stopwatch--speed-up .stopwatch__hand--minute {
  animation: 600s steps(60, end) infinite rotate-minute;
}

.stopwatch--speed-up .stopwatch__hand--second {
  animation: 10s linear infinite rotate-second;
}

@keyframes rotate-minute {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes rotate-second {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}
